import React from "react";
// import { MdAccountCircle, MdCheckCircle } from "react-icons/md";
import { connect } from "react-redux";
import {
  Auth,
  // DistrictList,
  // FC_GetDistrictList,
  // FT_GetAssignAssets,
  FC_GetAssignAssets,
  FC_GetUserProfile,
  UserDetailsByNID,
} from "../../actions";
import { AssignStuffComponent } from "../../components/AssignStuffComponent/AssignStuffComponent";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
// import Loading from "../../components/Loading/Loading";
// import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import { StoreState } from "../../reducers";
import Alert from "../../components/Alert/Alert";
import TextInput from "../../components/Inputs/TextInput";
import { AssignStaffToSchool } from "../UserProfile/AssignStaffToSchool";

// props for the component
interface AppProps {
  auth: Auth;
}

interface AppState {
  loading: boolean;
  error: {
    target: "main-success" | "main-error" | "nid" | null;
    msg: string;
  };
  nid: string;
  user_details: UserDetailsByNID | null;
  user_found: "FOUND" | "NOT-FOUND" | null;
}

class _RegisterForDataValidation extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      error: {
        msg: "",
        target: null,
      },
      nid: "",
      user_details: null,
      user_found: null,
    };
  }

  // componentDidMount = () => {};

  CheckNidHandler = (e: any) => {
    e.preventDefault();

    if (this.state.nid.length <= 4) {
      return this.setState({
        error: {
          target: "nid",
          msg: "NID or passport is required!",
        },
      });
    }

    this.setState({
      loading: true,
      user_found: null,
    });

    FC_GetUserProfile(this.state.nid, (status, data, msg) => {
      if (status) {
        this.setState({
          user_details: data,
          loading: false,
          user_found:
            data !== null && data.user_id !== null ? "FOUND" : "NOT-FOUND",
        });
      } else {
        this.setState({
          loading: false,
          user_found: "NOT-FOUND",
        });
      }
    });
  };

  clearError = () =>
    this.setState({
      error: {
        msg: "",
        target: null,
      },
    });

  render() {
    return (
      <>
        <div className=" bg-white p-2 flex items-center">
          {this.state.user_found !== null && (
            <Button
              onClick={() =>
                this.setState({
                  user_found: null,
                })
              }
              theme="default"
            >
              Back
            </Button>
          )}
          <h1 className="text-2xl font-bold pl-3">Create new staff</h1>
        </div>
        {this.state.error.target === "main-error" && (
          <Alert theme="danger" fixed onClick={this.clearError}>
            {this.state.error.msg}
          </Alert>
        )}
        {this.state.error.target === "main-success" && (
          <Alert theme="success" fixed onClick={this.clearError}>
            {this.state.error.msg}
          </Alert>
        )}

        <DashboardContainer>
          {this.state.user_found === null && (
            <form onSubmit={this.CheckNidHandler}>
              <div className="flex">
                <div className="flex-1">
                  <TextInput
                    error={
                      this.state.error.target === "nid"
                        ? this.state.error.msg
                        : ""
                    }
                    label="Nid/Passport"
                    name=""
                    onChange={(e) => this.setState({ nid: e.target.value })}
                    placeholder="Enter NID/Phone number"
                    value={this.state.nid}
                    disabled={this.state.loading}
                  />
                </div>
                <div className="pt-8 pl-2">
                  <Button loading={this.state.loading} type="submit">
                    Check Id
                  </Button>
                </div>
              </div>
            </form>
          )}

          {this.state.user_found !== null && (
            <>
              {this.state.user_found === "FOUND" &&
              this.state.user_details !== null &&
              this.state.user_details.user_id !== null ? (
                <div>
                  <AssignStaffToSchool
                    user_id={this.state.user_details.user_id}
                    // userInfo={this.state.userInfo}
                    userDetails={{
                      email: this.state.user_details.email
                        ? this.state.user_details.email
                        : "",
                      full_name: this.state.user_details?.full_name
                        ? this.state.user_details?.full_name
                        : "",
                      nid: this.state.user_details?.nid
                        ? this.state.user_details?.nid
                        : "",
                      staff_code: this.state.user_details?.staff_code
                        ? this.state.user_details?.staff_code
                        : "",
                    }}
                    close={() =>
                      this.setState({
                        user_found: null,
                      })
                    }
                  />
                </div>
              ) : (
                <div>
                  <AssignStuffComponent
                    // district_id={
                    //   this.props.auth.district?.district_code
                    //     ? this.props.auth.district?.district_code
                    //     : ""
                    // }
                    district_id={
                      this.props.auth.district?.district_code
                        ? this.props.auth.district?.district_code
                        : ""
                    }
                    select_district_active={true}
                    nid_value={this.state.nid}
                    assignType="REGISTER"
                    // school={
                    //   this.props.auth.school
                    //     ? {
                    //         school_id: this.props.auth.school.school_code,
                    //         school_name: this.props.auth.school.school_name,
                    //       }
                    //     : undefined
                    // }
                    exclude_roles={["3", "4", "18", "21", "22", "25", "26"]}
                    // position_code={accademicInfo.position_id}
                    // qualification_id={accademicInfo.qualification_id}
                    only_post_for_school={true}
                    user_id={""}
                    formAccess={{
                      first_name: true,
                      last_name: true,
                      full_name: true,
                      middle_name: true,
                      nid: true,
                      phone_numbers: true,
                      role_id: true,
                      country_id: false,
                      district_code: true,
                      sector_code: false,
                      school_code: true,
                      qualification_id: true,
                      academic_year_id: true,
                      stakeholder_id: false,
                      start_date_in_the_school: true,
                      position_id: true,
                    }}
                    required={{
                      first_name: true,
                      last_name: true,
                      full_name: true,
                      middle_name: false,
                      nid: true,
                      phone_numbers: true,
                      role_id: true,
                      country_id: false,
                      district_code: true,
                      sector_code: false,
                      school_code: true,
                      qualification_id: true,
                      academic_year_id: true,
                      stakeholder_id: false,
                      start_date_in_the_school: true,
                      position_id: false,
                    }}
                    success={(data) => {
                      //   setStaffCreated(true);
                      this.setState({
                        error: {
                          msg: "A new staff has been registered",
                          target: "main-success",
                        },
                      });
                    }}
                  />
                </div>
              )}
            </>
          )}
        </DashboardContainer>
      </>
    );
  }
}

const mapStateToProps = ({ auth }: StoreState): { auth: Auth } => {
  return { auth: auth };
};

export const RegisterForDataValidation = connect(mapStateToProps, {
  FC_GetAssignAssets,
})(_RegisterForDataValidation);
